html {
  font-size: 6.25%; /*  1rem = 1px */
  box-sizing: border-box; /*box mnodel*/
}

*,
*:before,
*:after {
  box-sizing: inherit; /*box mnodel*/
  margin: 0;
  padding: 0;
}

body {
  font-size: 18rem;
  font-family: "Space Grotesk", sans-serif;
  height: 100vh;
}

h1,
h2,
h3 {
  color: #000000;
}

h1 {
  font-size: 40rem;
}

h2 {
  font-size: 36rem;
}

h3 {
  font-size: 30rem;
}

p {
  font-size: 20rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.webp .contenedor {
  background-image: url("../img/bg-main-mobile.webp");
}
@media (min-width: 992px) {
  .webp .contenedor {
    background-image: url("../img/bg-main-desktop.webp");
  }
}

.no-webp .contenedor {
  background-image: url("../img/bg-main-mobile.jpg");
}
@media (min-width: 992px) {
  .no-webp .contenedor {
    background-image: url("../img/bg-main-desktop.jpg");
  }
}

.pr {
  position: relative;
}

.contenedor {
  width: 100%;
  padding: 20rem;
  background-repeat: no-repeat;
}
@media (min-width: 992px) {
  .contenedor {
    display: flex;
    align-items: center;
    height: 100%;
    width: 70%;
  }
}

.cards {
  top: 0;
  left: 0;
  margin-top: 10rem;
  width: 100%;
}

.contenedor-card {
  width: 100%;
}

.card-p {
  width: 290rem;
  border-radius: 10rem;
}
@media (min-width: 992px) {
  .card-p {
    width: 450rem;
  }
}

.card-back {
  position: absolute;
  right: 20rem;
  -webkit-box-shadow: 29px 29px 101px -50px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 29px 29px 101px -50px rgba(0, 0, 0, 0.75);
  box-shadow: 29px 29px 101px -50px rgba(0, 0, 0, 0.75);
}
@media (min-width: 992px) {
  .card-back {
    left: 260rem;
    top: 400rem;
  }
}
.card-back span {
  position: absolute;
  top: 73rem;
  right: 35rem;
  color: #ffffff;
  font-size: 10rem;
  letter-spacing: 1rem;
}
@media (min-width: 992px) {
  .card-back span {
    top: 112rem;
    right: 60rem;
    font-size: 18rem;
  }
}

.card-front {
  position: absolute;
  top: 120rem;
  left: 20rem;
  -webkit-box-shadow: 29px 29px 101px -50px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 29px 29px 101px -50px rgba(0, 0, 0, 0.75);
  box-shadow: 29px 29px 101px -50px rgba(0, 0, 0, 0.75);
}
@media (min-width: 992px) {
  .card-front {
    left: 150rem;
  }
}

.card-fornt-info {
  position: absolute;
  top: 0rem;
  padding: 20rem;
  width: 100%;
}
.card-fornt-info .circulos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 55rem;
  margin-bottom: 30rem;
}
@media (min-width: 992px) {
  .card-fornt-info .circulos {
    width: 85rem;
    margin-bottom: 80rem;
  }
}
.card-fornt-info .circulos .c1 {
  background-color: #ffffff;
  width: 30rem;
  height: 30rem;
  border-radius: 50%;
}
@media (min-width: 992px) {
  .card-fornt-info .circulos .c1 {
    width: 50rem;
    height: 50rem;
  }
}
.card-fornt-info .circulos .c2 {
  width: 15rem;
  height: 15rem;
  border: 1rem solid hsl(270deg, 3%, 87%);
  border-radius: 50%;
}
@media (min-width: 992px) {
  .card-fornt-info .circulos .c2 {
    width: 20rem;
    height: 20rem;
  }
}
.card-fornt-info .card-number {
  display: flex;
  margin-bottom: 20rem;
}
.card-fornt-info .card-number p {
  color: #ffffff;
  letter-spacing: 1rem;
}
@media (min-width: 992px) {
  .card-fornt-info .card-number p {
    font-size: 32rem;
  }
}
.card-fornt-info .card-datos {
  display: flex;
  justify-content: space-between;
}
.card-fornt-info .card-datos p {
  text-transform: uppercase;
  font-size: 10rem;
  color: hsl(270deg, 3%, 87%);
  letter-spacing: 1rem;
}
@media (min-width: 992px) {
  .card-fornt-info .card-datos p {
    font-size: 16rem;
  }
}

.form-content {
  margin-top: 250rem;
}

.form {
  margin-top: 250rem;
}
@media (min-width: 992px) {
  .form {
    margin-top: -330rem;
    width: 350rem;
  }
}

.inputs {
  margin-bottom: 20rem;
  display: flex;
  flex-direction: column;
  gap: 10rem;
}
.inputs:hover {
  cursor: pointer;
}
.inputs label {
  color: hsl(278deg, 68%, 11%);
  text-transform: uppercase;
  font-size: 10rem;
  font-weight: 700;
  letter-spacing: 1rem;
}
@media (min-width: 992px) {
  .inputs label {
    font-size: 13rem;
    font-weight: 500;
  }
}
.inputs input {
  width: 100%;
  padding: 10rem;
  border-radius: 5rem;
  border: 1px solid hsl(270deg, 3%, 87%);
  color: hsl(278deg, 68%, 11%);
  font-size: 12rem;
}
@media (min-width: 992px) {
  .inputs input {
    font-size: 16rem;
  }
}
.inputs input:focus {
  border: 1px solid hsl(278deg, 68%, 11%);
}

.date {
  flex-direction: row;
  margin-bottom: 0;
}
.date div {
  width: 20%;
}
.date div label {
  display: block;
  margin-bottom: 10rem;
}
.date div:nth-child(3) {
  flex-grow: 1;
}

.btn {
  width: 100%;
  padding: 15rem 0;
  border: none;
  background-color: hsl(278deg, 68%, 11%);
  color: #ffffff;
  border-radius: 5rem;
}
.btn:hover {
  cursor: pointer;
}

.inputError {
  border: 2rem solid red !important;
}

.m {
  color: red;
  font-size: 14rem;
  display: none;
}

.date-m {
  margin: 15rem 0;
}

.mensaje {
  display: none;
  margin-top: 300rem;
  text-align: center;
}
@media (min-width: 992px) {
  .mensaje {
    margin-top: 0rem;
    width: 400rem;
  }
}
.mensaje h1 {
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 2rem;
  margin-bottom: 15rem;
}
.mensaje p {
  color: hsl(279deg, 6%, 55%);
  margin-bottom: 50rem;
}
.mensaje .btn {
  margin-bottom: 320rem;
}